import React from 'react';
import {NavLink} from 'react-router-dom';
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";
const Navb=()=>
{
    return(
        <>
        <div className="container-fluid nav bg">
         <div className="row">
          <div className="col-10 mx-auto">

        
                <nav className="navbar navbar-expand-lg navbar-light bg-light">
              
      <h1 className="navbar-brand" href="#">AK </h1>
      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav ml-auto">
          <li className="nav-item active">
            <NavLink activeClassName="menu_active" className="nav-link" to="/" exact>Home <span className="sr-only">(current)</span></NavLink>
          </li>
          <li className="nav-item">
            <NavLink  activeClassName="menu_active" className="nav-link" to="/service" exact>Service</NavLink>
          </li>
          <li className="nav-item">
            <NavLink  activeClassName="menu_active" className="nav-link" to="/about" exact>About</NavLink>
          </li>
          <li className="nav-item">
            <NavLink  activeClassName="menu_active" className="nav-link" to="/contact" exact>Contact</NavLink>
          </li>
        
      
        </ul>
      
      </div>
      </nav>
          </div>

    </div>

    </div>
            </>
    )
}

export default Navb;